@import './variables';
@import './responsiveness';

.form-sign-up-section {
  display: none;
  transition: all 0.5s ease-in-out;
  position: fixed;
  z-index: 9999999;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.9);

  .sign__form-bg {
    width: 100%;
    height: 100%;

    .form_white {
      background-color: $background-color;
      height: 600px;
      width: 400px;
      margin: 0 auto; // Center the form horizontally

      @media (max-width: 750px) {
        width: 90%; // Adjust the width for smaller screens
        height: 100%; // Remove fixed height to allow content to adjust
      }

      @media (max-width: 450px) {
        width: 98%; // Adjust the width for even smaller screens
        height: 100%;
      }

      .form_wrapper {
        width: 100%;
        height: 100%;

        #my-form {
          width: 100%;
          height: 100%;
          transition: all 0.5s ease-in;

          span {
            border: 1px solid $text-color;
            position: absolute;
            left: 50%; // Center horizontally
            transform: translateX(-50%); // Center horizontally
            border-radius: 50%;
            height: 60px;
            justify-content: center;
            align-items: center;
            text-align: center;
            place-content: center;
            width: 60px;
            top: 2rem;

            .fa-times {
              margin-top: 15px;
              font-size: 1.7rem;
              color: $text-color;
            }
          }

          .form_inputs {
            place-content: center;
            padding-top: 10rem;
            display: block;
            align-items: center;
            justify-content: center;
            text-align: center;
            cursor: pointer;

            input {
              padding: 1rem 1rem; // Reduce padding for smaller screens
              margin-bottom: 20px;
              outline: none;
              font-size: $small;

              &:hover {
                background-color: lightblue;
              }
            }

            .form_btn {
              padding: 1rem 4rem; // Reduce padding for smaller screens
              font-size: 1.3rem;
              color: $dark-text;
              cursor: pointer;
              outline: none;
              border: none;
              border-radius: 5px;
            }
          }
        }
      }
    }
  }
}




