.swiper-scrollbar {
  display: none;
}

.swiper-slide {
  width: 200px;
  height: 200px;
  object-fit: contain;

  img {
    width: 250px;
    height: 250px;
  }
}

.swiper-button-next {
  background-color: forestgreen;
  padding: 2rem;
  fill: #fff;
}

.swiper-button-prev {
  background-color: forestgreen;
  padding: 2rem;
  fill: #fff;

}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.fade-in {
  animation: fadeIn 1.5s ease-in-out;
}

/* Offset to trigger animation slightly before element comes into view */
.fade-in-offset {
  opacity: 5;
  transform: translateY(20px);
}

.swiper-slide {
  position: relative;
  overflow: hidden;
  /* Ensure child elements don't overflow */
}

.content-flu {
  background-color: rgba(0, 0, 0, 0.3);
  width: 88%;
  height: 97%;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  transition: opacity 0.3s ease;
  /* Add a smooth transition effect */
  place-content: center;
  justify-content: center;
  align-items: center;

  h2 {
    text-align: center;
    font-size: 1rem;
    padding: 1rem;
    background-color: forestgreen;
    color: #fff;

  }
}

.swiper-slide:hover .content-flu {
  opacity: 1;
  /* Adjust the desired opacity on hover */
}


